export default {
	name: 'businessPutIn',
	data() {
		return {
			dataLists: [], // 城防多选的转型后数组
			dataList: [], // 城防多选的转型后数组
			
			selcetDataList: [], // 盛放选中的商机
			check:  this.Static+'home/extendManageList/dataReport/check.png', // type 选择按钮的未选中状态 img
			checked:  this.Static+'home/extendManageList/dataReport/checked.png', // type 选择按钮的选中状态 img
			checkedItems:  this.Static+'home/extendManageList/dataReport/checkedItems.png', // 子项选中对勾 img
			clickItemsIndex: '', // 记录子项点击的index
			selectStyle2: { // 选择之后样式边框变化
				// background: '#02C2A2',
				color: '#02C2A2',
				border:'1px solid #02C2A2'
			},
			noSelectStyle2:  { // 选择之前样式边框变化
				color: '#2E2E30',
			},
		};
	},
	components: {},
	computed: {},
	watch: {},
	created(){
		this.Keyword();
		
	},
	mounted() {},
	methods: {
		// 勾选商机
		labelBtn(name, index) {
			if (this.selcetDataList.join(',').indexOf(name) > -1) {
				this.selcetDataList.map((items, key) => {
					this.dataLists[index].checked = true;
				});
			} else {
				this.dataLists[index].checked = false;
			}
		},
		Keyword(){
			if (uni.getStorageSync('landRegist')) {
				let landRegistLG = JSON.parse(uni.getStorageSync('landRegist')); // 读取缓存的用户信息
				console.log(landRegistLG.user.id);
				// let params = {}; // 请求总数居时 参数为空
				uni.showLoading({ // 展示loading
					title: '加载中'
				});
				uni.request({
					url: this.api2 + '/rest-rp/user/'+landRegistLG.user.id, //接口地址。
					// data: params,
					method: 'GET',
					header: {
						Authorization: "Bearer " + landRegistLG.token //将token放到请求头中
					},
					success: (response) => {
						uni.hideLoading();
						console.log(response.data);
						this.dataList = response.data.content.pushKeys;
						if(response.data.ret==200){
							this.userKeys();
						}
					},
					fail: (error) => {
						uni.hideLoading(); // 隐藏 loading
						uni.showToast({
							title: '网络繁忙，请稍后',
							icon: 'none',
							duration: 1000
						});
						console.log(error, '网络繁忙，请稍后');
					}
				});
			}
		},
		checkboxChange: function(e) {
			this.selcetDataList = e.detail.value; // 获取选中的值
			this.$emit("event1",this.selcetDataList)
			console.log(this.selcetDataList, '--------------------selcetDataList-----------------------');
		},
		userKeys(){
			if (uni.getStorageSync('landRegist')) {
				let landRegistLG = JSON.parse(uni.getStorageSync('landRegist')); // 读取缓存的用户信息
				console.log(landRegistLG.user.id);
				// let params = {}; // 请求总数居时 参数为空
				uni.showLoading({
					// 展示loading
					title: '加载中'
				});
				uni.request({
					url: this.api2 + '/rest-rp/userKeys', //接口地址。
					// data: params,
					method: 'GET',
					header: {
						Authorization: 'Bearer ' + landRegistLG.token //将token放到请求头中
					},
					success: response => {
						uni.hideLoading();
						console.log(response.data);
						// this.dataLists=response.data.content;
						response.data.content.map((items, index)=>{
							let item = {
								value: items,
								code: index,
								checked: false
							};
							this.dataLists.push(item)
						});
						
						this.dataLists.map((itemww,index)=>{
							if(itemww.value==this.dataList[0]){
								itemww.checked = true;
								this.selcetDataList.push(itemww.value)
							}else if(itemww.value==this.dataList[1]){
								itemww.checked = true;
								this.selcetDataList.push(itemww.value)
							}else if(itemww.value==this.dataList[2]){
								itemww.checked = true;
								this.selcetDataList.push(itemww.value)
							}else if(itemww.value==this.dataList[3]){
								itemww.checked = true;
								this.selcetDataList.push(itemww.value)
							}else if(itemww.value==this.dataList[4]){
								itemww.checked = true;
								this.selcetDataList.push(itemww.value)
							}else if(itemww.value==this.dataList[5]){
								itemww.checked = true;
								this.selcetDataList.push(itemww.value)
							}else if(itemww.value==this.dataList[6]){
								itemww.checked = true;
								this.selcetDataList.push(itemww.value)
							}else if(itemww.value==this.dataList[7]){
								itemww.checked = true;
								this.selcetDataList.push(itemww.value)
							}else if(itemww.value==this.dataList[8]){
								itemww.checked = true;
								this.selcetDataList.push(itemww.value)
							}else if(itemww.value==this.dataList[9]){
								itemww.checked = true;
								this.selcetDataList.push(itemww.value)
							}else if(itemww.value==this.dataList[10]){
								itemww.checked = true;
								this.selcetDataList.push(itemww.value)
							}else if(itemww.value==this.dataList[11]){
								itemww.checked = true;
								this.selcetDataList.push(itemww.value)
							}else if(itemww.value==this.dataList[12]){
								itemww.checked = true;
								this.selcetDataList.push(itemww.value)
							}
						});
						console.log(this.selcetDataList)
						this.$emit("event1",this.selcetDataList)
					},
					fail: error => {
						uni.hideLoading(); // 隐藏 loading
						uni.showToast({
							title: '网络繁忙，请稍后',
							icon: 'none',
							duration: 1000
						});
						console.log(error, '网络繁忙，请稍后');
					}
				});
			}
		},
	}
};
